import React from 'react'
import { getImage } from 'gatsby-plugin-image'
import { Container, Flex, Box } from 'theme-ui'
import ContentText from '@solid-ui-components/ContentText'
import Reveal from '@solid-ui-components/Reveal'
import WithDefaultContent from '@solid-ui-blocks/WithDefaultContent'
import './about_mission.css';

const styles = {
  subTitle: {
    maxWidth: 500
  },
  content: {
    flexDirection: `column`,
    size: `full`,
    alignItems: `center`,
    justifyContent: `center`,
    py: 0
  },
  overlay: {
    size: `full`,
    bg: `omegaDarker`,
    opacity: 0.75,
    position: `absolute`,
    top: 0,
    zIndex: -1
  },
  image: {
    height:`full`,
    position: `absolute`,
    top: 0,
    zIndex: -2
  }
}

const AboutMission = ({
  content: { container, text, buttons, form, images }
}) => (
  <Container className="about-mission" variant='full' sx={{ textAlign: `center`, position: `relative` }}>
    <Flex
      sx={{
        ...styles.content
      }}
    >
      <Reveal effect='fadeInDown'>
        
        <div className="container ">
          <div className="row gy-4">
        
            <div className="col-lg-4 d-flex flex-column justify-content-center text-left">
              <div class="feature-block">
                <div class="css-icon">
                  <img src="data:image/png;base64," alt="Our Mission" height="60" />
                </div>
                <h5 class="text-left mb-1">Our Mission</h5>
                <p class="text-left max1100">Our mission is providing email marketing industry with most advance application/ tools, to maximize results, with minimum manual effort.</p>
              </div>
            </div>

            <div className="col-lg-4 d-flex flex-column justify-content-center text-left">
              <div class="feature-block">
                <div class="css-icon">
                  <img src="data:image/png;base64," alt="Our Vision" height="60" />
                </div>
                <h5 class="text-left mb-1">Our Vision</h5>
                <p class="text-left max1100">Our vision is to keep our core customer-centric approach fixed, while our business strategies evolve and adapt to ever changing tech space.</p>
              </div>
            </div>

            <div className="col-lg-4 d-flex flex-column justify-content-center text-left">
              <div class="feature-block">
                <div class="css-icon">
                  <img src="data:image/png;base64," alt="Our Team" height="60" />
                </div>
                <h5 class="text-left mb-1">Our Team</h5>
                <p class="text-left max1100">Our vision is to keep our core customer-centric approach fixed, while our business strategies evolve and adapt to ever changing tech space.</p>
              </div>
            </div>

          </div>
        </div>
          
      </Reveal>
    </Flex>
  </Container>
)

export default WithDefaultContent(AboutMission)
